<template>
  <div class="custom-sub-menu-container">
    <CustomMenu :list="routerList" :isGroup="true"></CustomMenu>
    <div class="main-container sub-menu-context">
      <router-view></router-view>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { authRouter, LocalStorage } from "jz-tool-lib";
import billManagement from "@/router/menu-router/financial-center/bill-management";
import CustomMenu from "@/components/common/custom-sub-menu.vue";

defineOptions({
  name: "financialCenter.billManagement",
});

const agentInfo = LocalStorage.getItem("bd_agentInfo");
const list = billManagement.filter((item) => {
  if (agentInfo.ecSwitch === 0) {
    return item.name !== "financialCenter.billManagement.customerLedger";
  }
  return item;
});
const routerList = authRouter(list ?? [], ["账单管理"]);
</script>
